/* Media queries
 * ======================================================================== */
.action-button {
  background-image: -webkit-linear-gradient(150deg, #EF5FA7 6%, #517CFF 100%);
  background-image: -o-linear-gradient(150deg, #EF5FA7 6%, #517CFF 100%);
  background-image: linear-gradient(-60deg, #EF5FA7 6%, #517CFF 100%);
  border-radius: 60px;
  -webkit-box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.33);
          box-shadow: 2px 2px 6px 1px rgba(0, 0, 0, 0.33);
  text-align: center;
  color: #fff;
  font-size: 24px;
  line-height: 44px;
  letter-spacing: 2px;
  padding: 12px 24px; }

@media screen and (min-width: 768px) {
  .action-button {
    font-size: 40px;
    line-height: 56px; } }



